
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

.message {
    padding: 8px;
    border-radius: 4px;
    background-color: var(--bg-secondary);
    margin-bottom: 4px;

    // All nested messages use theme-appropriate background color
    .message__replies & {
        background-color: var(--bg-tertiary, rgba(128, 128, 128, 0.1));
    }

    &__header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 6px;
    }

    &__author {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 6px;
    }

    &__name {
        font-weight: 500;
        font-size: 14px;
    }

    &__notice {
        color: var(--text-secondary);
        font-size: 12px;
    }

    &__badge {
        padding: 1px 4px;
        border-radius: 3px;
        font-size: 11px;

        &--private {
            background-color: #f8d7da;
            color: #721c24;
        }
    }

    &__meta {
        text-align: right;
        color: var(--text-secondary);
        font-size: 12px;
    }

    &__content {
        margin-bottom: 6px;
        color: var(--text-primary);
        word-break: break-word;
        font-size: 14px;

        ::v-deep p {
            margin-bottom: 0.5em;
        }

        ::v-deep img {
            max-width: 100%;
            border-radius: 4px;
            margin: 8px 0;
        }

        ::v-deep a {
            color: var(--primary);
            text-decoration: none;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__actions {
        display: flex;
        gap: 10px;
        margin-bottom: 6px;
    }

    &__action {
        color: var(--primary);
        cursor: pointer;
        font-size: 12px;

        &:hover {
            text-decoration: underline;
        }

        &--read {
            color: #28a745;

            &:hover {
                color: #218838;
            }
        }

        &--loading {
            opacity: 0.7;
            cursor: not-allowed;

            &:hover {
                text-decoration: none;
            }
        }
    }

    &__replies {
        margin-top: 6px;
        margin-left: 12px;
        border-left: 2px solid var(--border-color);
        padding-left: 8px;
    }

    &__reply {
        margin-bottom: 4px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__show-replies {
        margin-bottom: 6px;
    }

    &__show-replies-btn,
    &__load-more-btn {
        background-color: transparent;
        border: none;
        color: var(--primary);
        cursor: pointer;
        font-size: 12px;
        padding: 3px 0;

        &:hover {
            text-decoration: underline;
        }
    }

    &__load-more {
        margin-top: 6px;
        text-align: center;
    }

    .replies-container-enter-active,
    .replies-container-leave-active {
        transition: all 0.3s ease-out;
        overflow: hidden;
        max-height: 2000px;
        /* Arbitrary large value */
    }

    .replies-container-enter,
    .replies-container-leave-to {
        max-height: 0;
        opacity: 0;
    }

    .reply-enter-active {
        animation: slide-in 0.3s ease-out forwards;
    }

    .reply-move {
        transition: transform 0.3s ease-out;
    }

    @keyframes slide-in {
        0% {
            opacity: 0;
            transform: translateX(-10px);
        }

        100% {
            opacity: 1;
            transform: translateX(0);
        }
    }

    .message__reply {
        animation: fade-in 0.3s ease-out forwards;
    }

    @keyframes fade-in {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }
}
