
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

.discussion {
    margin-top: 10px;

    &__warning {
        background-color: var(--lighter-primary);
        padding: 12px;
        border-radius: 4px;
        margin-bottom: 15px;
        font-weight: 500;
        font-size: 14px;
    }

    &__empty {
        padding: 20px 0;
        text-align: center;
        color: var(--text-secondary);
        font-size: 14px;
    }

    &__loading {
        display: flex;
        justify-content: center;
        padding: 20px 0;
    }

    &__messages {
        margin-top: 25px;
        margin-bottom: 20px;
    }

    &__message-wrapper {
        margin-bottom: 8px;
    }

    &__comment-buttons {
        display: flex;
        justify-content: center;
        gap: 10px;
        margin-bottom: 20px;
    }

    &__comment-button {
        min-width: 150px;
    }

    &__name-form {
        margin: 0 auto 15px;
        max-width: 500px;
        text-align: center;

        h3 {
            margin-bottom: 8px;
            font-weight: 500;
            font-size: 15px;
        }
    }

    &__name-input {
        display: flex;
        gap: 8px;
        justify-content: center;
    }

    &__disabled {
        padding: 20px;
        text-align: center;
        color: var(--text-secondary);
        background-color: var(--bg-secondary);
        border-radius: 5px;
    }
}

// Reply type selector modal styles
.reply-type-selector {
    padding: 20px;

    &__title {
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 10px;
        color: var(--text-primary);
    }

    &__text {
        margin-bottom: 20px;
        color: var(--text-secondary);
        font-size: 14px;
    }

    &__buttons {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__button {
        width: 100%;
    }
}

.message-enter-active,
.message-leave-active {
    transition: all 0.5s;
}

.message-enter,
.message-leave-to {
    opacity: 0;
    transform: translateY(-20px);
}

.discussion__message-wrapper--new {
    animation: highlight-new-message 2s ease-out;
}

@keyframes highlight-new-message {
    0% {
        background-color: rgba(var(--primary-rgb, 0, 123, 255), 0.2);
        transform: translateY(-10px);
    }

    70% {
        background-color: rgba(var(--primary-rgb, 0, 123, 255), 0.05);
    }

    100% {
        background-color: transparent;
        transform: translateY(0);
    }
}

.muted-placeholder {
    ::placeholder {
        color: var(--text-tertiary, #999);
        opacity: 0.6;
    }
}
