@import "../assets/variables";

.checkbox {
  cursor: pointer;
  display: flex;
  align-items: center;
}

input {
  display: none;

  &:checked + .box:before {
    content: '';
    display: block;
    width: 9px;
    height: 9px;
    background-color: $primary;
    border-radius: 1px;
  }
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 20px;
  height: 20px;
  border: 1px solid $primary;
  margin-right: 7px;
  border-radius: 3px;
}
