
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

.report-modal {
    &__title {
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 15px;
        color: var(--text-primary);
    }

    &__form {
        margin-bottom: 20px;
    }

    &__label {
        display: block;
        margin-bottom: 5px;
        font-size: 14px;
        color: var(--text-primary);
    }

    &__textarea {
        width: 100%;
        margin-bottom: 10px;
    }

    &__notice {
        font-size: 12px;
        color: var(--text-secondary);
        line-height: 1.4;
    }

    &__footer {
        display: flex;
        justify-content: flex-end;
        gap: 10px;
    }
}
