
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

.composer-modal {
    &__title {
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 15px;
        color: var(--text-primary);
    }

    &__form {
        margin-bottom: 20px;
    }

    &__textarea {
        width: 100%;
        margin-bottom: 10px;
        min-height: 120px;
    }

    &__form-notice {
        font-size: 14px;
        color: var(--text-secondary);
        margin-bottom: 10px;
    }

    &__form-row {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 15px;
        flex-wrap: wrap;
        gap: 15px;
    }

    &__form-actions {
        display: flex;
        gap: 15px;
    }

    &__form-action {
        display: flex;
        align-items: center;
        gap: 5px;
        cursor: pointer;
        color: var(--primary);
        padding: 5px 8px;
        border-radius: 4px;

        &:hover {
            background-color: var(--bg-secondary, rgba(0, 0, 0, 0.05));
        }

        img {
            width: 16px;
            height: 16px;
            filter: brightness(0) saturate(100%) var(--invert-icon, invert(0%));
        }
    }

    &__replying-to {
        display: flex;
        justify-content: space-between;
        padding: 8px 10px;
        background-color: var(--bg-secondary);
        border-radius: 4px;
        margin-bottom: 12px;
        font-size: 13px;

        span {
            font-weight: 500;
        }

        &-cancel {
            cursor: pointer;
            color: var(--primary);

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__footer {
        display: flex;
        justify-content: flex-end;
        gap: 10px;
        margin-top: 10px;
    }

    &__form-private {
        min-width: 120px;
        padding: 5px 0;
        font-size: 14px;
    }
}
